@import './fonts.scss';
@import './material.scss';

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  border: none;
  outline: none;
  font-family: "Avenir_Next_Regular";
  color: #333333;
}

b {
  font-family: "Avenir_Next_Demi_Bold";
}

strong{
  font-family: 'Avenir_Next_Bold';
}
.clear {
  clear: both;
}

.divGrowEjemplos {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  align-items: stretch;
  padding-top: 10px;
  background-color: white;
}

.divCol2Ejemplos {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.divCol2Ejemplos > .col2, .divGrowEjemplos > .col2 { 
  width: 49.5%;
  margin: 20px 0px;
}

// TABS
.mat-tab-label {
  width: 24.5%;
  background-color: #d8d8d8;
  opacity: 1 !important;
}

.mat-tab-labels {
  justify-content: space-between;
}

.mat-tab-nav-bar, .mat-tab-header {
  border-bottom: unset !important;
}

.mat-tab-label-content{font-size:1.2em; }

.mat-tab-label-active {
  background-color: #333333 !important;
  color: white !important;
  -moz-box-shadow: 0 5px 15px rgba(17, 17, 17, 0.2);
  -webkit-box-shadow: 0 5px 15px rgba(17, 17, 17, 0.2);
  box-shadow: 0 5px 15px rgba(17, 17, 17, 0.2);
  transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
}

.mat-tab-header {
  overflow: unset !important;
}

.mat-tab-label-container {
  overflow: unset !important;
}

.mat-ink-bar {
  background-color: unset !important;
}

.mat-button, .mat-icon-button, .mat-stroked-button, .mat-flat-button {
  line-height: 16px !important;
}

.mat-tab-header {
  display: flex;
  overflow: hidden;
  position: relative;
  flex-shrink: 0;
  overflow: auto!important;
  height: 100%!important;
  margin-bottom: 15px!important;
  padding-bottom: 0px!important;
  width: 100%!important;
}

.mat-tab-header-pagination-chevron{
  display:none;
}

.mat-tab-label{
  padding: 0 40px!important;
}

//Slider
.slide{
  background-color: white!important;
}

.carousel .control-dots .dot {
 box-shadow: none;
  width: 16px;
  height: 16px;
}

.carousel .control-dots .dot{
  border: solid #00a648 3px !important;
  background: unset !important;
  opacity: 1;

  &.selected{
    background: #00a648 !important;
  }

  &:focus{
    outline: none;
  }
}

//Divider
.MuiDivider-root{
  display: block;
  border-top-width: 2px!important;
  border-top-style: solid!important;
  width: 80%;
  margin: 0 auto!important;
  border-color: #00a648!important;
}

@media screen and (min-width: 701px){
  .MuiDivider-root{
    display: none;
  }
}

@media screen and (max-width:700px) {
  div.name{
    display: none;
  }
  .divCol2Ejemplos > .col2, .divGrowEjemplos > .col2 { 
    width: 98%;
    border-left: unset !important;
  }
  .divGrowEjemplos {
    display: unset;
  }
  .MuiDivider-root{
    display: block;
  }
}
