.tb_aviso{
    width: 100%;
    height:260px;
    display: table;
    vertical-align: middle;
    margin:0 auto;
    background-color:#00a940; 
    color:#ffffff;
}
.cll_aviso{
    text-align: center;
    vertical-align: middle;
    display: table-cell;
}

.titulo_error {
    font-size:6em;
    transition: all 0.3s ease-out;
	-moz-transition: all 0.3s ease-out;
	-webkit-transition: all 0.3s ease-out;
}
.subtitulo_error {
    font-size:2.6em;
    transition: all 0.3s ease-out;
	-moz-transition: all 0.3s ease-out;
	-webkit-transition: all 0.3s ease-out;
}

.tb_leyenda_aviso{
    width: 100%;
    display: table;
    vertical-align: middle;
    margin:0 auto;
    background-color:#ffffff; 
    color:#00a940; 
}

.cll_leyenda_aviso{
    text-align: center;
    vertical-align: middle;
    display: table-cell;
}

.descripcion_error {
    font-size:4em;
}
.descripcion_lite {
    font-size:2.6em;
}

.bt_imagen_error{
    width: 100%;
    display: table;
    vertical-align: middle;
    margin:0 auto;
    color:#ffffff;

}

.cll_imagen_error{
    text-align: center;
    vertical-align: middle;
    display: table-cell;
    padding: 70px 0px 30px 0px;
}
.cll_imagen_error img{
    width:50%;
    display:block;
    margin:0 auto;
    transition: all 0.3s ease-out;
	-moz-transition: all 0.3s ease-out;
	-webkit-transition: all 0.3s ease-out;
}


@media screen and (max-width:780px){
    .titulo_error{
        font-size:4em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }

    .subtitulo_error {
        font-size:2em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }
    .descripcion_error{
        font-size:3em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }
}



@media screen and (max-width:560px){
    .descripcion_error{
        font-size:2.1em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }
    .descripcion_lite{
        font-size:1.9em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }
}
@media screen and (max-width:460px){
    .titulo_error{
        font-size:3em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }

    .subtitulo_error {
        font-size:1.6em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }
}

@media screen and (max-width:400px){
    .descripcion_error{
        font-size:1.7em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;

    }

    .descripcion_lite{
        font-size:1.4em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;

    }

}

@media screen and (max-width:340px){
    .titulo_error{
        font-size:2.6em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }

    .subtitulo_error {
        font-size:1.2em;
        transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
    }

}