
html,
body{
	width:100%;
	height:100%;  
	margin: 0;
	padding: 0;
	list-style: none;
	text-decoration: none;
	border: none;
	outline: none;
	font-family:'Avenir_Next_Regular';
}

    b{font-family:'Avenir_Next_Demi_Bold';}

    .clear{clear:both;}

	.w50{
		width:50%;
		float:left;
	}

	.btn_redondo{
		text-decoration: none;
		background-color: #00a940;
		border: 2px solid #00a940;
		color: #ffffff;
		padding: 9px 9px;
		min-width: 280px;
		display: inline-block;
		outline: none;
		border-radius:40px 40px 40px 40px; 
		font-size:1em; 
		transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
		background-color: #00a648;
		color: white;
		border-radius: 50px !important;
		font-size: 1.3em;
		padding: 8px 10px !important;
		width: 100%;
		letter-spacing: 0.5px;
		line-height: 1.2 !important;
		max-width: 450px;
		display: flex;
		flex-direction: column;
		text-align: center;
	}

	.btn_redondo div:nth-child(2){
		font-family:'Avenir_Next_Demi_Bold';
		font-size:1.2em;  
	}
	.btnCuadrado{
		background-color: #00a648;
		color: white;
		border-radius: unset !important;
		font-size: 1.2em;
		padding: 10px 10px !important;
		width: 225px;
		font-family: "Avenir_Next_Regular";
		letter-spacing: 0.5px;
		float: right;
		border:none;
		line-height: 36px;
	}

	.btnCuadrados{
		background-color:#e1251b;
		color: white;
		font-weight: bold;
		border-radius: unset !important;
		font-size: 1.2em;
		padding: 10px 10px !important;
		font-family: "Avenir_Next_Regular";
		float: right;
		border:none;
	}
	
	.un_descuento{
		display: flex;
		flex-direction: column;
	}

	// .btnCuadrado.vC{float:right;}

	.titulo_encabezado{
		text-align:center;
		font-size:3.5em;
		color:#ffffff;
		padding:20px 0px 20px 0px;
		background-color:#00a940;
		transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
		box-sizing: border-box;
		
	}

	.contenedorGeneral{
		width: 90%;
		max-width:860px; 
		min-width:360px; 
		margin:0 auto;
	}

	.credito {
		padding:26px 0px 26px 0px;
		box-sizing:border-box;
		font-size: 2.1em;
		text-align:center;
		transition: all 0.3s ease-out;
		-moz-transition: all 0.3s ease-out;
		-webkit-transition: all 0.3s ease-out;
	}
	.credito img{
		width: 128px;
		display: inline-block;
		padding-left: 10px;
		vertical-align: middle;
		margin-bottom: 10px;
	
	}
	.tb_credito{
		width:100%;
		display:table;
		padding: 0px 5px;
		box-sizing: border-box;
	}
	.cll_credito{
        display:table-cell;
		vertical-align:middle;
	}

	.informacionPlan{
		height: 100%;
		padding: 30px;
		background-color:#ffffff;
		position:relative; 
	}

	.contenedorInfo{
		background: #f5f5f5;
		margin-top: 2rem;
		padding: 20px;
	}

	.tabContenido {
		width:100%;
		height: 100%;
		display: table;
		vertical-align: middle;
		margin: 4% auto;

	}
	.cllContenido{
		text-align: center;
		vertical-align: middle;
		display: table-cell;
	}

	.modeloTelefono img{
		display:block;
		width:285px;
		max-width:285px;
		margin: 0 auto; 
	}

	.precioTotal{
		text-align:center;
		position:relative; 
		font-family:'Avenir_Next_Demi_Bold'; 
	}
	.precioTotal div{display:inline-block;}

	.titulo_precio_total{
		font-size:1em;
		padding-right: 2px;
	}
	.signoPesos{
		font-size:0.9em;
	}
	.precio{
		font-size:1em;
		padding-right:4px;
	}


	.nombreTelefono{
		position:relative;
		text-align:center;
		font-size:2em;
		font-family:'Avenir_Next_Demi_Bold'; 
	}
	.nombreTelefono div:nth-child(2){
		position: relative;
	
	}

	.titulo_te_reconocemos{
		text-align: center;
		font-size: 1.4em;
		margin: 10px 0px 14px 0px;
	}


	.tb_plazo_descuento{
		width:100%; 
		display:table;
		margin-bottom:40px;  
	}
	.cll_plazo_descuento{
		display:table-cell;
		vertical-align:top; 
		position:relative;
		text-align:center; 
	}
	.semana{
		font-size:1.8em;
		font-family:'Avenir_Next_Demi_Bold'; 
	}
	.pago_inicial span{
		display:inline-block;
		font-size:0.7em;
	}
	.tb_plazo_descuento div.cll_plazo_descuento:nth-child(1):after{
		content: "";
		position: absolute;
		right: 0;
		top: 0;
		width: 4px;
		height: 100%;
		background: #00a940;
	} 
	.precioUno{
		display:inline-block;
		font-size:1.1em;
	}

	.precioDos{
		display:inline-block;
		font-size: 1.8em;
		font-family: "Avenir_Next_Demi_Bold";
	}

	.precioDos  span:nth-child(1){
		font-size:0.7em;
	}

	.union{
		display:inline-block;
		padding: 10px 10px 0px 10px;
	}

	.precioContado,
	.precioParaTi,
	.a_solo
	{
		text-align:right;
	}


	.linea{
		background-image: url('http://portal.colaboracionsimba.net/DOCTOSDMAQ/2020/07Jul/REQDC7910/entregable/vista/linea.svg');
		//background-image:url("/linea.svg");
		background-size: 100%;
		background-position:center center;
		background-repeat: no-repeat;
	}

	.signoPesosDos{
		font-size:0.8em;
	}
	.signoPesosTres{
		font-size:1.2em;
		font-family: "Avenir_Next_Demi_Bold";
	}

	.precioContado{font-size:1.3em;}
	.a_solo{font-size:1.4em;}

	.precioParaTi{
		font-size:1.2em;
		font-family: "Avenir_Next_Demi_Bold";
	}
	
	.a_solo span:nth-child(2){
		font-size: 1.6em;
		font-family: "Avenir_Next_Demi_Bold";
	}

	.planesOui{
		padding: 26px 0px 10px 0px;
		font-size: 2.2em;
	}

	.planesOui img{
		width:70px;
		vertical-align: middle;
	}
	.tb_oui{
		width:100%;
		display:table;
		background-color: #f5f5f5;
	}

	.tb_oui div.cll_oui:nth-child(1){
		width:84%;
	}
	.cll_oui{
        display:table-cell;
		vertical-align:middle;
		font-size:0.89em;
	}
	.descripcion_planes{
		text-align:center;
		font-size:1.1em;
	}

	.lista_planes div.bloque_lista:last-child{
		border-bottom:1px solid transparent;
	}
	
	.img_auto{
		width:32px!important;
	}

	.bloque_lista{
    width: 100%;
    text-align: center;
    padding: 20px 0;
	border-bottom: 1px solid #cbcbcb;
	font-family: 'Avenir_Next_Regular';
	display: flex;
    justify-content: center;
    align-items: center;
     }

	.bloque_lista div{
		display:inline-block;
		text-align:center;
		font-size:1em;
	
	}

	.bloque_lista img{
		width: 18px;
		vertical-align: middle;
		margin: 4px;
	}


	.floatLeft {
		float: left;
	}


	.floatRight {
		float: right;
	}

	ul {
		margin-block-start: unset;
		margin-block-end: unset;
	}
	// .detallesTelefono{
	// 	padding-top: 20px;
	// }
	.pd_cero{margin:0px!important;}

	

