		}

	}

	@media screen and (max-width:820px){
		.nombreTelefono{
			font-size: 1.6em;
			text-align:left;
			transition: all 0.3s ease-out;
			-moz-transition: all 0.3s ease-out;
			-webkit-transition: all 0.3s ease-out;
			margin-left: -20px;
		}
		.floatRight {
			float: none;
		}
		.w50 {
			width: 100%;
		}
