			float: none;
			width: 50%;
			max-width: 450px;
			margin-left: 25%;
		}
		.btnCenter.pd_cero{width:100%;}
		.titulo_te_reconocemos {
			margin: 0px 0px 14px 0px;
		}
	}

	@media screen and (max-width:874px){
